<template>
	<view class="page">
		<view class="input-t">
			<text>标题</text>
			<text class="input-quick">{{repairDetail.articleTitle}}</text>
		</view>
		
		<view class="input-t">
			<text>内容</text>
			<text class="input-quick"></text>
		</view>
		<view class="input-body">
			<textarea :disabled="isDisable" v-model="form.articleContent" class="input-textare" />
		</view>
		
		
		
		<view class="uni-article__comment">
			<view class="uni-article__comment-box" v-for="comment in commentData" :key="comment.id" @tap="toReply(comment)">
				<view class="article-meta">
					<view class="article-header-image">
						<image class="image" :src="comment.sysUser.avatar" mode="widthFix"></image>
					</view>
					<view class="article-header-content">
						<view class="article-header-title">
							<text>{{comment.sysUser.userName}}</text>
						</view>
						<view class="article-header-info"> {{comment.createTime||''}}</view>
					</view>
				</view>
				<view class="uni-article__comment-centent">
					<user-reply v-if="comment.children.length > 0" :digest="comment.children"></user-reply>
					{{comment.content}}
				</view>
			</view>
		</view>
		
		
		<button
			class="confirm-btn"
			:class="'bg-' + themeColor.name"
			:disabled="btnLoading"
			:loading="btnLoading"
			@tap="send"
		>
			发表评论
		</button>

	</view>
</template>

<script>
	import moment from '@/common/moment';
	import requests from '@/utils/requests.js'
	
	import userReply from '@/components/reply/reply.vue'
	import commentData from './comment.js'
	export default {
		components: {
			userReply
		},
		data() {
			return {
				commentData: [],
				repairDetail:{},
				date: '',
				type: '',
				mtype: '',
				mtypeValue: '', // 选中下标
				sendDate: {
					remind: '0',
					content: ''
				},
				form: {
					id: '',
					articleTitle: '',
					articleContent: '',
				},
				worksType: [],
				worksTypeList: [],
				btnLoading: false,
				isDisable: true,
				searchParameters: {
					pageNum: 1,
					pageSize: 50,
					articleId: ''
				},
			};
		},
		onLoad(e) {
			this.repairDetail = JSON.parse(e.item)
			console.log(this.repairDetail)
			this.form.id = this.repairDetail.id
			this.form.articleTitle = this.repairDetail.articleTitle
			this.form.articleContent = this.repairDetail.articleContent
			this.searchParameters.articleId = this.repairDetail.id
			//this.getComment();
		},
		onShow() {
			uni.setNavigationBarColor({
				frontColor: '#ffffff',
				backgroundColor: this.themeColor.color,
				animation: {
					duration: 400,
					timingFunc: 'easeIn'
				}
			})
			this.getList()
		},

		methods: {
			
			getList() {
				let that = this
				requests.getCommentList(that.searchParameters).then(res => {
					if(res && res.code === 200){
						that.commentData = res.rows;
						// if(that.commentData.length === 0){
						// 	that.isEmpty = true;
						// }else{
						// 	that.isEmpty = false;
						// }
					}
					
				})
			},
			
			
			toReply(val){
				console.log(val)
				uni.navigateTo({
					url: './comment?repairDetail=' + JSON.stringify(val) + '&type=' + 2
				})
			},
			
			send(){
				uni.navigateTo({
					url: './comment?articleId=' + this.form.id + '&type=' + 1
				})
			},
			getComment() {
				this.commentData = commentData.items;
			}
		}
	};
</script>

<style lang="scss">
	page {
		background-color: $page-color-base;
	}

	.date {
		height: 80upx;
		line-height: 80upx;
		font-size: $font-base + 2upx;
		color: $font-color-dark;
	}

	/*表单标题*/
	.input-t {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: $spacing-base;
		margin-top: $spacing-base;
		font-size: $font-base;

	}

	.input-quick {
		position: relative;
		padding-right: 40upx;

		.iconfont {
			font-size: $font-sm;
		}
	}

	.input-body {
		background: $color-white;
		padding: $spacing-sm $spacing-base;

		.gender-item {
			margin-right: 20upx;

			.gender-item-text {
				padding-left: 10upx;
			}

		}
	}

	.input-textare {
		height: 200upx;
		font-size: 34upx;
		line-height: 50upx;
		width: 100%;
		box-sizing: border-box;
		padding: 20upx 30upx 0;
	}
</style>

<style scoped>
	.article-meta {
		padding: 20rpx 20rpx;
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: flex-start;
		color: gray;
	}

	.article-header-image {
		width: 60rpx;
		height: 60rpx;
		flex-shrink: 0;
		/* border-radius: 50%; */
		overflow: hidden;
	}

	.article-header-image .image {
		width: 100%;
		height: 100%;
	}

	.article-header-content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-left: 10px;
		font-size: 24rpx;
		line-height: 1;
		width: 100%;
		/* height: 60rpx; */
	}

	.article-header-title {
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 26rpx;
		line-height: 2;
		color: #000;
	}

	.article-header-info {
		font-size: 24rpx;
		line-height: 1;
	}

	.article-top {
		display: flex;
		align-items: flex-end;
		font-size: 26rpx;
		line-height: 1;
		color: #999;
	}

	.article-top .image {
		width: 30rpx;
		margin-left: 5rpx;
	}

	.uni-article__comment-title {
		width: 100%;
		line-height: 3;
		font-size: 28rpx;
		color: #666;
		background: #f5f5f5;
		padding-left: 20rpx;
	}

	.uni-article__comment-centent {
		border-bottom: 1px #eee solid;
		padding: 0 20rpx;
		padding-left: 95rpx;
		padding-bottom: 20rpx;
		font-size: 14px;
		color: #333;
	}
</style>
